import "../App.css";
import '../styles/instructions.css';
import React, { useEffect } from "react";
import { RxOpenInNewWindow } from "react-icons/rx";
import { FaArrowLeftLong, FaArrowRightLong } from "react-icons/fa6";

export default function Instructions({ openAllAnswers, backToStart }) {

    //Shortcuts
    const handleKeyPress = (e) => {
        if (e.key === "Escape") {
            backToStart();
        }
    };

    useEffect(() => {
        window.addEventListener("keydown", handleKeyPress);
        return () => {
            window.removeEventListener("keydown", handleKeyPress);
        };
    }, []);

    return (
        <div className="instruction-page background-game">
            <div className="instruction-content">
                <h2 className="title-position">Instructions</h2>
                <div className="border-shine instruction-background">
                    <h3>Hur man spelar</h3>
                    <p>Spelet r likt konceptet Jeopardy! och gr ut p att spelledaren sger svaren och spelarna stller frgorna. Detta r en webbapplikation som inte r anpassad fr att spela p en mobil. <a className="all-answers-link" onClick={openAllAnswers}>Alla svar<RxOpenInNewWindow /></a></p>
                    <table className="instruction-table">
                        <tbody>
                            <tr>
                                <td className="left">Buzzer:</td>
                                <td>Tips kan vara att anvnda ngon form av buzzer fr att avgra vilken spelare/lag som ska svara frst. Ex. <a className="all-answers-link" href="https://www.multibuzz.app/" target="_blank">multibuzz.app</a>.</td>
                            </tr>
                            <tr>
                                <td className="left">Spelare:</td>
                                <td>Brja med att lgga till minst 2 spelare/lag och ge dem namn.</td>
                            </tr>
                            <tr>
                                <td className="left">Tema:</td>
                                <td>Temat utgr tillhrande 5 kategorier med frgor.</td>
                            </tr>
                            <tr>
                                <td className="left">Frga:</td>
                                <td>Nr spelet brjar, kan frgor vljas frn spelplanen dr varje frga r frknippad med ett specifikt vrde (100500 pong).</td>
                            </tr>
                            <tr>
                                <td className="left">Svara:</td>
                                <td>
                                    Frst efter att spelledaren har sagt 'svaret' fr spelarna buzza fr att svara med en 'frga'.
                                    Det r viktigt att spelarnas svar stlls som en frga "Vad r..", "Vem r.." osv. Har en spelare buzzat mste spelaren svara. Lekledaren kan <a className="all-answers-link" onClick={openAllAnswers}>kontrollera svaren<RxOpenInNewWindow /></a> p separat skrm.
                                </td>
                            </tr>
                            <tr>
                                <td className="left">Pong:</td>
                                <td>
                                    Om en spelare svarar fel, fr de minuspong motsvarande frgans vrde och andra spelare fr en chans att svara (spelledare tilldelar detta p 'frgesidan').
                                    Om en spelare svarar rtt, fr de pluspong motsvarande frgans vrde (spelledare tilldelar detta p 'svarssidan').
                                </td>
                            </tr>
                            <tr>
                                <td className="left">Vinnare:</td>
                                <td>Den spelare/lag med flest pong nr alla frgor har besvarats vinner.</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <div className="new-content border-shine instruction-background">
                    <h3>Exempel</h3>
                    <p><b>Spelledarens 'svar': </b>Detta r Sveriges huvudstad.</p>
                    <p><b>Spelarens 'frga': </b>Vad r Stockholm?</p>
                </div>
                <div className="new-content border-shine instruction-background">
                    <h3>Genvgar</h3>
                    <table className="control-table">
                        <tbody>
                            <tr className="table-margin">
                                <td className="left"><kbd>F11</kbd></td>
                                <td>Helskrm</td>
                            </tr>
                            <tr className="table-margin">
                                <td className="left"><kbd>Esc</kbd></td>
                                <td>Tillbaka till fregende</td>
                            </tr>
                            <tr>
                                <td className="left"><kbd>Enter</kbd></td>
                                <td>Spela spel / Gr vidare till starta svar/spelplan</td>
                            </tr>
                            <tr>
                                <td className="left"><kbd>Ctrl</kbd> + <kbd><FaArrowLeftLong/></kbd> / <kbd><FaArrowRightLong/></kbd></td>
                                <td>Vxlar mellan teman i spelinstllningar</td>
                            </tr>
                            <tr>
                                <td className="left"><kbd>Ctrl</kbd> + <kbd>Enter</kbd></td>
                                <td>Starta spel</td>
                            </tr>
                            <tr>
                                <td className="left"><kbd>Tab</kbd></td>
                                <td>Tabbar igenom val av frgor i spelplan</td>
                            </tr>
                            <tr>
                                <td className="left"><kbd>1</kbd> - <kbd>9</kbd></td>
                                <td>Fr att ge + / - pong till (max 9) spelare (vnster till hger)</td>
                            </tr>
                            <tr>
                                <td className="left"><kbd>S</kbd></td>
                                <td>Visar scoreboard</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <div className="btn-instructions-container btn-top">
                    <button className="btn btn-arrow btn-instructions" onClick={backToStart}><FaArrowLeftLong /></button>
                </div>
            </div>
        </div>
    );
}

